@import "../mixins.scss";
.main-header {
  
  padding: 0 20px;
  z-index: 99;
  background-color: #212121;
  color: white;
  border-bottom: 1px solid;
  border-color: rgba(0, 0, 0, 0.1);
  @include md {
      padding: 0 40px;
     max-width: 100%;
  }
  .header-content {
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 50px;
     width:90% ;
      margin: 0 auto;
      @include md {
          height: 80px;
      }
  }
  .left {
      list-style-type: none;
      display: none;
      gap: 25px;
      @include md {
          display: flex;
      }
      li {
          font-size: 14px;
          font-weight: 600;
          text-transform: uppercase;
          cursor: pointer;
          &:hover {
              opacity: 0.6;
          }
      }
  }
  .center {
      font-size: 22px;
      font-weight: 700;
      cursor: pointer;
      @include md {
          font-size: 34px;
          position: absolute;
          left: 50%;
          transform: translateX(-50%);
      }
  }
  .right {
      display: flex;
      align-items: center;
      gap: 20px;
      @include md {
          gap: 25px;
      }
      svg {
          font-size: 20px;
          cursor: pointer;
          @include md {
              font-size: 24px;
          }
          &:hover {
              opacity: 0.6;
          }
      }
      .cart-icon {
          position: relative;
          span {
              min-width: 20px;
              text-align: center;
              background: #8e2de2;
              padding: 2.5px;
              position: absolute;
              top: -5px;
              right: -12px;
              font-size: 12px;
              line-height: 1;
              border-radius: 10px;
          }
      }
    }
  }